}

body {
  position: relative;
  height: 100%;
  min-width: 320px;
  overflow-x: hidden;
  color: #333;
  font-size: 16px;
  font-family: "Raleway", "Arial", sans-serif;
  line-height: 1;
}

body input, body textarea {
  border: #666 1px solid;
  outline: none;
}

body input:focus:required:invalid, body textarea:focus:required:invalid {
  border-color: red;
}

body input:required:valid, body textarea:required:valid {
  border-color: green;
}

img {
  width: 100%;
  max-width: 100%;
  height: auto;
}

.img-cover {
  display: flex;
  width: 100%;
  height: 100% !important;
  object-fit: cover;
}

b,
strong {
  font-weight: bold;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

a {
  display: block;
  color: #2e2e2e;
}

a:hover {
  text-decoration: none;
}

a:active, a:focus {
  border: none;
  outline: none;
}

button, figure {
  color: inherit;
  border: none;
  outline: none;
}

button:focus, button:active, figure:focus, figure:active {
  outline: none;
  border: none;
}

select:focus, select:active {
  outline: none;
}

header, section, footer {
  position: relative;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.wrapper {
  display: flex;
  flex-direction: column;
  width: 100vw;
  max-width: 100%;
  height: 100%;
  min-height: 100vh;
}

.page-footer {
  margin-top: auto;
}

body.ishome .page-header {
  height: 100vh;
  min-height: 400px;
}

/* Typography */
.h1-title,
.h2-title,
.h3-title,
.h4-title {
  font-family: "Raleway", "Arial", sans-serif;
}

.h1-title {
  font-size: 38px;
  font-weight: 800;
}

.h2-title {
  font-size: 30px;
  font-weight: 800;
}

.h3-title {
  font-size: 16px;
  font-weight: 700;
}

.h4-title {
  font-size: 22px;
  font-weight: 300;
}

.button {
  display: inline-block;
  color: #fff;
  background-color: #e7746f;
  padding: 15px 45px;
  font-size: 13px;
  text-transform: uppercase;
  font-weight: 700;
  border-radius: 2px;
  text-align: center;
  position: relative;
  transition: 0.25s ease;
  cursor: pointer;
}

.button:focus, .button:hover {
  text-decoration: none;
  color: #fff;
}

.button:hover {
  background-color: #eb8985;
}

.button:hover::after {
  opacity: .22;
}

.button:active {
  background-color: #e35f59;
}

.btn-scroll {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  cursor: pointer;
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.btn-scroll:after {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: block;
  width: 180%;
  height: 180%;
  content: '';
}

@keyframes move-arrow {
  0% {
    transform: translateY(-7px);
    opacity: 1;
  }
  15% {
    opacity: 1;
  }
  100% {
    transform: translateY(8px);
    opacity: 0;
  }
}

.page-header {
  display: flex;
  justify-content: center;
  align-content: center;
  background-attachment: fixed;
  width: 100vw;
  max-width: 100%;
}

.page-header-flex {
  display: flex;
  position: relative;
  top: 0;
  height: 100%;
  width: 100%;
}

.page-header-flex .flex-center {
  margin: auto;
}

.page-header-composition {
  text-align: center;
  color: #fcfcfc;
}

.page-header-composition p {
  font-size: 20px;
  margin-bottom: 0;
  font-weight: 300;
}

.page-header-composition .h1-title {
  display: block;
  text-transform: uppercase;
  font-weight: 800;
  padding: 22px 0;
  margin-bottom: 0;
}

.page-header-scroll-down {
  bottom: 25px;
  left: calc(50% - 25px);
  border: 2px solid #fff;
}

.page-header-scroll-down:active, .page-header-scroll-down:focus {
  border: 2px solid #fff;
}

.page-header-scroll-down .icon-arrow {
  font-size: 20px;
  animation: move-arrow 2s normal infinite;
}
